import React, { useState, useRef } from 'react';

function SendSMSPage() {
  //  
  const [selectedSender, setSelectedSender] = useState('');
  const [selectedRecipients, setSelectedRecipients] = useState([]);
  const [isRecipientModalOpen, setIsRecipientModalOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [activeTab, setActiveTab] = useState('groups'); // 'groups' or 'members'

  //   
  const [selectedTemplate, setSelectedTemplate] = useState('');
  const [category, setCategory] = useState('');
  const [subCategory, setSubCategory] = useState('');

  //  
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [attachedImage, setAttachedImage] = useState(null);
  
  //   ( )
  const [messageType, setMessageType] = useState('SMS');
  
  const fileInputRef = useRef(null);

  //  
  const groups = [
    { id: 1, name: '', count: 5 },
    { id: 2, name: '', count: 8 },
    { id: 3, name: '', count: 20 },
    { id: 4, name: 'VIP', count: 15 }
  ];

  const senderNumbers = [
    { id: 1, number: '02-1234-5678', name: '' },
    { id: 2, number: '070-1234-5678', name: '' },
    { id: 3, number: '010-1234-5678', name: '' }
  ];

  const members = [
    { id: 1, name: '', phone: '010-1234-5678', description: '', groupId: 1 },
    { id: 2, name: '', phone: '010-9876-5432', description: '', groupId: 2 },
    { id: 3, name: '', phone: '010-5555-1234', description: '', groupId: 2 },
    { id: 4, name: '', phone: '010-1111-2222', description: 'VIP', groupId: 4 },
    { id: 5, name: '', phone: '010-3333-4444', description: '', groupId: 1 }
  ];

  const templates = [
    { 
      id: 1, 
      name: '  ', 
      category: '', 
      subCategory: '/',
      content: '[] 50%   !   .',
      title: '  '
    },
    { 
      id: 2, 
      name: '  ', 
      category: '', 
      subCategory: '/',
      content: '  2  A  .  .',
      title: '  '
    },
    { 
      id: 3, 
      name: '  ', 
      category: '', 
      subCategory: '/',
      content: '   .  .',
      title: ' '
    }
  ];

  //    ( 2,  1)
  const getByteLength = (str) => {
    let byte = 0;
    for (let i = 0; i < str.length; i++) {
      if (str.charCodeAt(i) > 127) {
        byte += 2;
      } else {
        byte += 1;
      }
    }
    return byte;
  };

  //    
  const updateMessageType = (content, hasImage) => {
    if (hasImage) {
      setMessageType('MMS');
    } else {
      const byteLength = getByteLength(content);
      if (byteLength <= 80) {
        setMessageType('SMS');
      } else if (byteLength <= 2000) {
        setMessageType('LMS');
      } else {
        setMessageType('MMS');
      }
    }
  };

  //   
  const handleContentChange = (e) => {
    const newContent = e.target.value;
    setContent(newContent);
    updateMessageType(newContent, attachedImage !== null);
  };

  //   
  const handleTemplateSelect = (templateId) => {
    const template = templates.find(t => t.id === parseInt(templateId));
    if (template) {
      setSelectedTemplate(templateId);
      setCategory(template.category);
      setSubCategory(template.subCategory);
      setContent(template.content);
      setTitle(template.title);
      updateMessageType(template.content, attachedImage !== null);
    } else {
      setSelectedTemplate('');
      setCategory('');
      setSubCategory('');
    }
  };

  //   
  const handleImageAttach = (e) => {
    const file = e.target.files[0];
    if (file) {
      setAttachedImage(file);
      updateMessageType(content, true);
    }
  };

  //   
  const handleImageRemove = () => {
    setAttachedImage(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
    updateMessageType(content, false);
  };

  const currentByteLength = getByteLength(content);

  //   
  const openRecipientModal = () => {
    setIsRecipientModalOpen(true);
  };

  const closeRecipientModal = () => {
    setIsRecipientModalOpen(false);
  };

  const toggleRecipient = (recipient) => {
    setSelectedRecipients(prev => {
      const exists = prev.find(r => r.id === recipient.id);
      if (exists) {
        return prev.filter(r => r.id !== recipient.id);
      } else {
        return [...prev, recipient];
      }
    });
  };

  const addGroupRecipients = (groupId) => {
    const groupMembers = members.filter(member => member.groupId === groupId);
    setSelectedRecipients(prev => {
      const newRecipients = [...prev];
      groupMembers.forEach(member => {
        if (!newRecipients.find(r => r.id === member.id)) {
          newRecipients.push(member);
        }
      });
      return newRecipients;
    });
  };

  const removeRecipient = (recipientId) => {
    setSelectedRecipients(prev => prev.filter(r => r.id !== recipientId));
  };

  //   
  const filteredGroups = groups.filter(group =>
    group.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const filteredMembers = members.filter(member =>
    member.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    member.phone.includes(searchQuery) ||
    member.description.toLowerCase().includes(searchQuery.toLowerCase())
  );

  return (
    <>
      <section id="main-content">
        <h1>SMS/MMS </h1>

        <p className="main-description">
          SMS, LMS, MMS     .          .
        </p>

        <div className="alert alert-info">
          <strong>  :</strong> 
            80   LMS ,   MMS .
        </div>

        <h2 id="message-types">  </h2>
        <div className="card">
          <div className="card-title">SMS, LMS, MMS </div>
          
          <div className="grid-container">
            <div className="card feature-card">
              <div className="card-title">
                <span className="feature-icon"></span>
                SMS (Short Message Service)
              </div>
              <p><strong>:</strong> 80 ( 40,  80)</p>
              <p><strong>:</strong>    </p>
              <p><strong>:</strong>  </p>
              <ul className="checklist">
                <li>  </li>
                <li>  </li>
                <li>   </li>
              </ul>
            </div>

            <div className="card feature-card">
              <div className="card-title">
                <span className="feature-icon"></span>
                LMS (Long Message Service)
              </div>
              <p><strong>:</strong> 2,000 ( 1,000,  2,000)</p>
              <p><strong>:</strong>    </p>
              <p><strong>:</strong> SMS </p>
              <ul className="checklist">
                <li>  </li>
                <li>   </li>
                <li>  </li>
              </ul>
            </div>

            <div className="card feature-card">
              <div className="card-title">
                <span className="feature-icon"></span>
                MMS (Multimedia Message Service)
              </div>
              <p><strong>:</strong> 2,000 + </p>
              <p><strong>:</strong>     </p>
              <p><strong>:</strong>  </p>
              <ul className="checklist">
                <li>  </li>
                <li>  </li>
                <li>,  </li>
              </ul>
            </div>
          </div>
        </div>

        <h2 id="send-settings"> </h2>
        <div className="card">
          <div className="card-title">    </div>
          
          <div style={{display: 'grid', gap: '20px'}}>
            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                  
              </label>
              
              <div style={{marginBottom: '12px'}}>
                <button 
                  className="btn btn-secondary"
                  onClick={openRecipientModal}
                  style={{width: '100%', justifyContent: 'space-between'}}
                >
                  <span>
                    {selectedRecipients.length > 0 
                      ? `${selectedRecipients.length} ` 
                      : '  '
                    }
                  </span>
                  <span></span>
                </button>
              </div>

              {selectedRecipients.length > 0 && (
                <div className="card" style={{background: 'var(--bg-secondary)', marginBottom: '16px'}}>
                  <div className="card-title" style={{fontSize: '1rem', marginBottom: '12px'}}>
                       ({selectedRecipients.length})
                  </div>
                  <div style={{display: 'flex', flexWrap: 'wrap', gap: '8px'}}>
                    {selectedRecipients.map(recipient => (
                      <div key={recipient.id} style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: '8px',
                        background: 'white',
                        padding: '6px 12px',
                        borderRadius: 'var(--radius-sm)',
                        fontSize: '0.9rem',
                        border: '1px solid var(--border-light)'
                      }}>
                        <span>{recipient.name} ({recipient.phone})</span>
                        <button
                          onClick={() => removeRecipient(recipient.id)}
                          style={{
                            background: 'none',
                            border: 'none',
                            color: 'var(--text-muted)',
                            cursor: 'pointer',
                            padding: '0',
                            fontSize: '0.8rem'
                          }}
                        >
                          
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                 
              </label>
              <select
                value={selectedSender}
                onChange={(e) => setSelectedSender(e.target.value)}
                style={{
                  width: '100%',
                  padding: '12px',
                  border: '1px solid var(--border-color)',
                  borderRadius: 'var(--radius-md)',
                  fontSize: '14px'
                }}
              >
                <option value=""> </option>
                {senderNumbers.map(sender => (
                  <option key={sender.id} value={sender.id}>
                    {sender.number} ({sender.name})
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        <h2 id="template-selection"> </h2>
        <div className="card">
          <div className="card-title">   </div>
          <p>        .</p>
          
          <div style={{marginBottom: '20px'}}>
            <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                ()
            </label>
            <select
              value={selectedTemplate}
              onChange={(e) => handleTemplateSelect(e.target.value)}
              style={{
                width: '100%',
                padding: '12px',
                border: '1px solid var(--border-color)',
                borderRadius: 'var(--radius-md)',
                fontSize: '14px'
              }}
            >
              <option value="">  (  )</option>
              {templates.map(template => (
                <option key={template.id} value={template.id}>
                  {template.name} ({template.category} &gt; {template.subCategory})
                </option>
              ))}
            </select>
          </div>

          {selectedTemplate && (
            <div className="alert alert-success">
              <strong>  !</strong><br/>
                 ,   .
            </div>
          )}
        </div>

        <h2 id="category-settings"> </h2>
        <div className="card">
          <div className="card-title"> </div>
          
          <div style={{display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '16px'}}>
            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                
              </label>
              <input
                type="text"
                value={category}
                onChange={(e) => setCategory(e.target.value)}
                placeholder=": , , "
                style={{
                  width: '100%',
                  padding: '12px',
                  border: '1px solid var(--border-color)',
                  borderRadius: 'var(--radius-md)',
                  fontSize: '14px'
                }}
              />
            </div>
            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                
              </label>
              <input
                type="text"
                value={subCategory}
                onChange={(e) => setSubCategory(e.target.value)}
                placeholder=": /, /, /"
                style={{
                  width: '100%',
                  padding: '12px',
                  border: '1px solid var(--border-color)',
                  borderRadius: 'var(--radius-md)',
                  fontSize: '14px'
                }}
              />
            </div>
          </div>
        </div>

        <h2 id="message-content"> </h2>
        <div className="card">
          <div className="card-title">
               
            <span style={{
              background: messageType === 'SMS' ? '#10b981' : messageType === 'LMS' ? '#f59e0b' : '#ef4444',
              color: 'white',
              padding: '4px 12px',
              borderRadius: '12px',
              fontSize: '0.8rem',
              marginLeft: '12px'
            }}>
              : {messageType}
            </span>
          </div>
          
          <div style={{marginBottom: '16px'}}>
            <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
               {messageType === 'SMS' && <span style={{color: 'var(--text-muted)', fontSize: '0.9rem'}}>(SMS   )</span>}
            </label>
            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder={messageType === 'SMS' ? ' SMS  ' : '  '}
              disabled={messageType === 'SMS'}
              style={{
                width: '100%',
                padding: '12px',
                border: '1px solid var(--border-color)',
                borderRadius: 'var(--radius-md)',
                fontSize: '14px',
                backgroundColor: messageType === 'SMS' ? 'var(--bg-secondary)' : 'white'
              }}
            />
          </div>

          <div style={{marginBottom: '16px'}}>
            <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
               
              <span style={{float: 'right', fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                {currentByteLength} / {messageType === 'SMS' ? '80' : '2,000'} 
              </span>
            </label>
            <textarea
              value={content}
              onChange={handleContentChange}
              placeholder="  "
              rows="6"
              style={{
                width: '100%',
                padding: '12px',
                border: '1px solid var(--border-color)',
                borderRadius: 'var(--radius-md)',
                fontSize: '14px',
                resize: 'vertical'
              }}
            />
            
            {currentByteLength > 80 && messageType !== 'MMS' && (
              <div className="alert alert-warning" style={{marginTop: '8px'}}>
                <strong>  :</strong> 
                 80  LMS  . (  )
              </div>
            )}
            
            {currentByteLength > 2000 && (
              <div className="alert alert-warning" style={{marginTop: '8px'}}>
                <strong>  :</strong> 
                 2,000 .   MMS .
              </div>
            )}
          </div>

          <div>
            <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                (MMS )
            </label>
            <div style={{display: 'flex', alignItems: 'center', gap: '12px'}}>
              <input
                type="file"
                ref={fileInputRef}
                accept="image/*"
                onChange={handleImageAttach}
                style={{display: 'none'}}
              />
              <button
                className="btn btn-secondary"
                onClick={() => fileInputRef.current?.click()}
              >
                 
              </button>
              
              {attachedImage && (
                <div style={{display: 'flex', alignItems: 'center', gap: '8px'}}>
                  <span style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                    {attachedImage.name}
                  </span>
                  <button
                    className="btn btn-secondary"
                    style={{padding: '4px 8px', fontSize: '0.8rem'}}
                    onClick={handleImageRemove}
                  >
                    
                  </button>
                </div>
              )}
            </div>
            
            {attachedImage && (
              <div className="alert alert-info" style={{marginTop: '8px'}}>
                <strong> MMS :</strong> 
                  MMS  . (  )
              </div>
            )}
          </div>
        </div>

        <h2 id="send-preview"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="card" style={{background: 'var(--bg-secondary)'}}>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '16px'}}>
              <div className="card-title" style={{margin: 0}}></div>
              <span className="status-badge success">{messageType}</span>
            </div>
            
            <div style={{background: 'white', padding: '16px', borderRadius: 'var(--radius-md)', border: '1px solid var(--border-color)'}}>
              {(messageType === 'LMS' || messageType === 'MMS') && title && (
                <div style={{fontWeight: '600', marginBottom: '8px', color: 'var(--text-primary)'}}>
                  {title}
                </div>
              )}
              
              <div style={{color: 'var(--text-secondary)', lineHeight: '1.6', marginBottom: attachedImage ? '12px' : '0'}}>
                {content || '   .'}
              </div>
              
              {attachedImage && (
                <div style={{
                  width: '100px',
                  height: '100px',
                  background: 'var(--bg-tertiary)',
                  borderRadius: 'var(--radius-sm)',
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontSize: '0.9rem',
                  color: 'var(--text-muted)'
                }}>
                   
                </div>
              )}
            </div>
            
            <div style={{marginTop: '12px', fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
              <div> : {selectedRecipients.length > 0 ? `${selectedRecipients.length}` : ''}</div>
              <div>: {selectedSender ? senderNumbers.find(s => s.id == selectedSender)?.number : ''}</div>
              <div> : {messageType} ({currentByteLength})</div>
            </div>
          </div>
          
          <div style={{display: 'flex', gap: '12px', marginTop: '20px'}}>
            <button className="btn btn-primary" style={{flex: 1}}>
               
            </button>
            {/* <button className="btn btn-secondary">
               
            </button>
            <button className="btn btn-secondary">
              
            </button> */}
          </div>
        </div>

        <h2 id="usage-guide"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="step-item">
            <div className="step-title">
              <div className="step-number">1</div>
               
            </div>
            <p>    .</p>
            <ul className="checklist">
              <li>      </li>
              <li>     </li>
              <li>       </li>
              <li>   (,  )</li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">2</div>
                ()
            </div>
            <p>      .</p>
            <ul className="checklist">
              <li>   ,   </li>
              <li>   </li>
              <li>   </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">3</div>
               
            </div>
            <p>  ,   .</p>
            <ul className="checklist">
              <li>SMS: 80 ,   </li>
              <li>LMS: 2,000 ,   </li>
              <li>MMS:     </li>
              <li>      </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">4</div>
                 
            </div>
            <p>    .</p>
            <ul className="checklist">
              <li>    </li>
              <li>     </li>
              <li>   </li>
            </ul>
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="card">
            <div className="card-title">Q. SMS  LMS  .</div>
            <p><strong>A.</strong>   80( 40)   LMS .    LMS  .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.   MMS  .</div>
            <p><strong>A.</strong> MMS SMS/LMS  .     .    LMS   .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.      ?</div>
            <p><strong>A.</strong> ,    , ,     .    .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q. SMS    ?</div>
            <p><strong>A.</strong> SMS  (80)    .    LMS  .</p>
          </div>
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>SMS/MMS       .</p>
          
          <div className="grid-container">
            <div className="card">
              <div className="card-title">  </div>
              <p>support@mbaas.com</p>
              <p>24  </p>
            </div>
          </div>
        </div>

        {/*     */}
        {isRecipientModalOpen && (
          <div style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 1000
          }}>
            <div style={{
              backgroundColor: 'white',
              borderRadius: 'var(--radius-lg)',
              width: '90%',
              maxWidth: '1000px',
              height: '80%',
              maxHeight: '700px',
              display: 'flex',
              flexDirection: 'column',
              boxShadow: 'var(--shadow-lg)'
            }}>
              {/*   */}
              <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                padding: '20px',
                borderBottom: '1px solid var(--border-color)'
              }}>
                <h3 style={{margin: 0, color: 'var(--text-primary)'}}>  </h3>
                <button
                  onClick={closeRecipientModal}
                  style={{
                    background: 'none',
                    border: 'none',
                    fontSize: '1.5rem',
                    cursor: 'pointer',
                    color: 'var(--text-muted)'
                  }}
                >
                  
                </button>
              </div>

              {/*   */}
              <div style={{
                display: 'flex',
                flex: 1,
                overflow: 'hidden'
              }}>
                {/* :     */}
                <div style={{
                  flex: 1,
                  padding: '20px',
                  borderRight: '1px solid var(--border-color)',
                  overflow: 'auto',
                  display: 'flex',
                  flexDirection: 'column'
                }}>
                  <h4 style={{marginTop: 0, color: 'var(--text-primary)'}}>  </h4>
                  
                  {/*   */}
                  <div style={{marginBottom: '20px'}}>
                    <input
                      type="text"
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      placeholder=", , ,  ..."
                      style={{
                        width: '100%',
                        padding: '12px',
                        border: '1px solid var(--border-color)',
                        borderRadius: 'var(--radius-md)',
                        fontSize: '14px'
                      }}
                    />
                  </div>

                  {/*   */}
                  <div style={{
                    display: 'flex',
                    marginBottom: '20px',
                    background: 'var(--bg-secondary)',
                    borderRadius: 'var(--radius-md)',
                    padding: '4px'
                  }}>
                    <button
                      onClick={() => setActiveTab('groups')}
                      style={{
                        flex: 1,
                        padding: '8px 16px',
                        border: 'none',
                        borderRadius: 'var(--radius-sm)',
                        background: activeTab === 'groups' ? 'var(--primary-color)' : 'transparent',
                        color: activeTab === 'groups' ? 'white' : 'var(--text-secondary)',
                        cursor: 'pointer',
                        fontSize: '0.9rem',
                        fontWeight: '500',
                        transition: 'all 0.2s'
                      }}
                    >
                       ({filteredGroups.length})
                    </button>
                    <button
                      onClick={() => setActiveTab('members')}
                      style={{
                        flex: 1,
                        padding: '8px 16px',
                        border: 'none',
                        borderRadius: 'var(--radius-sm)',
                        background: activeTab === 'members' ? 'var(--primary-color)' : 'transparent',
                        color: activeTab === 'members' ? 'white' : 'var(--text-secondary)',
                        cursor: 'pointer',
                        fontSize: '0.9rem',
                        fontWeight: '500',
                        transition: 'all 0.2s'
                      }}
                    >
                       ({filteredMembers.length})
                    </button>
                  </div>

                  {/*   */}
                  <div style={{flex: 1, overflow: 'auto'}}>
                    {activeTab === 'groups' && (
                      <div>
                        {filteredGroups.length === 0 ? (
                          <div style={{
                            textAlign: 'center',
                            color: 'var(--text-muted)',
                            padding: '40px 20px'
                          }}>
                            {searchQuery ? '  ' : ' '}
                          </div>
                        ) : (
                          filteredGroups.map(group => (
                            <div key={group.id} style={{
                              display: 'flex',
                              justifyContent: 'space-between',
                              alignItems: 'center',
                              padding: '12px',
                              background: 'var(--bg-secondary)',
                              borderRadius: 'var(--radius-md)',
                              marginBottom: '8px'
                            }}>
                              <div>
                                <strong>{group.name}</strong>
                                <span style={{color: 'var(--text-secondary)', marginLeft: '8px'}}>
                                  ({group.count})
                                </span>
                              </div>
                              <button
                                className="btn btn-primary"
                                style={{padding: '6px 12px', fontSize: '0.8rem'}}
                                onClick={() => addGroupRecipients(group.id)}
                              >
                                 
                              </button>
                            </div>
                          ))
                        )}
                      </div>
                    )}

                    {activeTab === 'members' && (
                      <div>
                        {filteredMembers.length === 0 ? (
                          <div style={{
                            textAlign: 'center',
                            color: 'var(--text-muted)',
                            padding: '40px 20px'
                          }}>
                            {searchQuery ? '  ' : ' '}
                          </div>
                        ) : (
                          filteredMembers.map(member => (
                            <div key={member.id} style={{
                              display: 'flex',
                              justifyContent: 'space-between',
                              alignItems: 'center',
                              padding: '12px',
                              background: selectedRecipients.find(r => r.id === member.id) 
                                ? 'rgba(59, 130, 246, 0.1)' 
                                : 'var(--bg-secondary)',
                              borderRadius: 'var(--radius-md)',
                              marginBottom: '8px',
                              border: selectedRecipients.find(r => r.id === member.id) 
                                ? '1px solid var(--primary-color)' 
                                : '1px solid var(--border-light)'
                            }}>
                              <div>
                                <div><strong>{member.name}</strong></div>
                                <div style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                                  {member.phone} | {member.description}
                                </div>
                              </div>
                              <button
                                className={`btn ${selectedRecipients.find(r => r.id === member.id) ? 'btn-primary' : 'btn-secondary'}`}
                                style={{padding: '6px 12px', fontSize: '0.8rem'}}
                                onClick={() => toggleRecipient(member)}
                              >
                                {selectedRecipients.find(r => r.id === member.id) ? '' : ''}
                              </button>
                            </div>
                          ))
                        )}
                      </div>
                    )}
                  </div>
                </div>

                {/* :    */}
                <div style={{
                  flex: 1,
                  padding: '20px',
                  overflow: 'auto'
                }}>
                  <h4 style={{marginTop: 0, color: 'var(--text-primary)'}}>
                       ({selectedRecipients.length})
                  </h4>
                  
                  {selectedRecipients.length === 0 ? (
                    <div style={{
                      textAlign: 'center',
                      color: 'var(--text-muted)',
                      padding: '40px 20px'
                    }}>
                        
                    </div>
                  ) : (
                    <div>
                      {selectedRecipients.map(recipient => (
                        <div key={recipient.id} style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          alignItems: 'center',
                          padding: '12px',
                          background: 'var(--bg-primary)',
                          border: '1px solid var(--border-color)',
                          borderRadius: 'var(--radius-md)',
                          marginBottom: '8px'
                        }}>
                          <div>
                            <div><strong>{recipient.name}</strong></div>
                            <div style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                              {recipient.phone} | {recipient.description}
                            </div>
                          </div>
                          <button
                            onClick={() => removeRecipient(recipient.id)}
                            style={{
                              background: 'none',
                              border: 'none',
                              color: 'var(--text-muted)',
                              cursor: 'pointer',
                              padding: '4px',
                              borderRadius: '4px',
                              fontSize: '1.2rem'
                            }}
                          >
                            
                          </button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>

              {/*   */}
              <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                padding: '20px',
                borderTop: '1px solid var(--border-color)',
                background: 'var(--bg-secondary)'
              }}>
                <div style={{color: 'var(--text-secondary)'}}>
                   {selectedRecipients.length} 
                </div>
                <div style={{display: 'flex', gap: '12px'}}>
                  <button className="btn btn-secondary" onClick={closeRecipientModal}>
                    
                  </button>
                  <button className="btn btn-primary" onClick={closeRecipientModal}>
                      ({selectedRecipients.length})
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </section>
    </>
  );
}

export default SendSMSPage;