import React, { useState } from 'react';

function MessageGroupPage() {
  const [members, setMembers] = useState([
    { id: 1, name: '', phone: '010-1234-5678', description: '' },
    { id: 2, name: '', phone: '010-9876-5432', description: '' },
    { id: 3, name: '', phone: '010-5555-1234', description: '' }
  ]);
  
  const [groups, setGroups] = useState([
    { id: 1, name: '', memberIds: [1] },
    { id: 2, name: '', memberIds: [2] },
    { id: 3, name: '', memberIds: [1, 2, 3] }
  ]);

  const [newMember, setNewMember] = useState({ name: '', phone: '', description: '' });
  const [newGroupName, setNewGroupName] = useState('');
  const [selectedGroupId, setSelectedGroupId] = useState(null);
  const [isAddingMember, setIsAddingMember] = useState(false);
  const [isAddingGroup, setIsAddingGroup] = useState(false);

  const addMember = () => {
    if (newMember.name && newMember.phone) {
      setMembers([...members, {
        id: Date.now(),
        ...newMember
      }]);
      setNewMember({ name: '', phone: '', description: '' });
      setIsAddingMember(false);
    }
  };

  const deleteMember = (id) => {
    setMembers(members.filter(member => member.id !== id));
    setGroups(groups.map(group => ({
      ...group,
      memberIds: group.memberIds.filter(memberId => memberId !== id)
    })));
  };

  const addGroup = () => {
    if (newGroupName) {
      setGroups([...groups, {
        id: Date.now(),
        name: newGroupName,
        memberIds: []
      }]);
      setNewGroupName('');
      setIsAddingGroup(false);
    }
  };

  const toggleMemberInGroup = (groupId, memberId) => {
    setGroups(groups.map(group => {
      if (group.id === groupId) {
        const memberIds = group.memberIds.includes(memberId)
          ? group.memberIds.filter(id => id !== memberId)
          : [...group.memberIds, memberId];
        return { ...group, memberIds };
      }
      return group;
    }));
  };

  const getGroupMembers = (groupId) => {
    const group = groups.find(g => g.id === groupId);
    return members.filter(member => group.memberIds.includes(member.id));
  };

  return (
    <>
      <section id="main-content">
        <h1>   </h1>

        <p className="main-description">
                 .         .
        </p>

        <div className="alert alert-info">
          <strong>   :</strong> 
                  .
        </div>

        <h2 id="member-management"> </h2>
        <div className="card">
          <div className="card-title"> </div>
          
          <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px'}}>
            <p>     .</p>
          </div>

          <div className="api-table">
            <table style={{width: '100%'}}>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {members.map(member => (
                  <tr key={member.id}>
                    <td><strong>{member.name}</strong></td>
                    <td>{member.phone}</td>
                    <td>{member.description}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        <h2 id="group-management"> </h2>
        <div className="card">
          <div className="card-title"> </div>
          
          <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px'}}>
            <p>      .</p>
          </div>

          {isAddingGroup && (
            <div className="card" style={{marginBottom: '20px', background: 'var(--bg-secondary)'}}>
              <div className="card-title">  </div>
              <div style={{marginBottom: '16px'}}>
                <label style={{display: 'block', marginBottom: '4px', fontWeight: '600'}}></label>
                <input
                  type="text"
                  value={newGroupName}
                  onChange={(e) => setNewGroupName(e.target.value)}
                  placeholder="  "
                  style={{
                    width: '100%',
                    padding: '8px 12px',
                    border: '1px solid var(--border-color)',
                    borderRadius: 'var(--radius-md)',
                    fontSize: '14px'
                  }}
                />
              </div>
              <div style={{display: 'flex', gap: '8px'}}>
                <button className="btn btn-primary" onClick={addGroup}></button>
                <button 
                  className="btn btn-secondary" 
                  onClick={() => setIsAddingGroup(false)}
                >
                  
                </button>
              </div>
            </div>
          )}

          <div className="grid-container">
            {groups.map(group => (
              <div key={group.id} className="card">
                <div className="card-title">
                  {group.name}
                  <span style={{
                    background: 'var(--primary-color)',
                    color: 'white',
                    padding: '2px 8px',
                    borderRadius: '12px',
                    fontSize: '0.8rem',
                    marginLeft: '8px'
                  }}>
                    {group.memberIds.length}
                  </span>
                </div>
                
                <div style={{marginBottom: '16px'}}>
                  <strong>:</strong>
                  <div style={{marginTop: '8px'}}>
                    {getGroupMembers(group.id).map(member => (
                      <span key={member.id} style={{
                        display: 'inline-block',
                        background: 'var(--bg-secondary)',
                        padding: '4px 8px',
                        borderRadius: 'var(--radius-sm)',
                        fontSize: '0.9rem',
                        margin: '2px 4px 2px 0'
                      }}>
                        {member.name} ({member.phone})
                      </span>
                    ))}
                    {group.memberIds.length === 0 && (
                      <span style={{color: 'var(--text-muted)', fontSize: '0.9rem'}}>
                         
                      </span>
                    )}
                  </div>
                </div>

                {selectedGroupId === group.id && (
                  <div style={{
                    marginTop: '16px',
                    padding: '16px',
                    background: 'var(--bg-secondary)',
                    borderRadius: 'var(--radius-md)'
                  }}>
                    <div className="card-title" style={{marginBottom: '12px', fontSize: '1rem'}}>
                       
                    </div>
                    <div className="checkbox-list">
                      {members.map(member => (
                        <div key={member.id} className="checkbox-item">
                          <input
                            type="checkbox"
                            checked={group.memberIds.includes(member.id)}
                            onChange={() => toggleMemberInGroup(group.id, member.id)}
                          />
                          <span>{member.name} ({member.phone})</span>
                        </div>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>

        <h2 id="usage-guide"> </h2>
        <div className="card">
          <div className="card-title"> </div>
          
          <div className="step-item">
            <div className="step-title">
              <div className="step-number">1</div>
               
            </div>
            <p>     .</p>
            <ul className="checklist">
              <li>    </li>
              <li>       </li>
              <li> 010-0000-0000  </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">2</div>
               
            </div>
            <p>     .</p>
            <ul className="checklist">
              <li>, , , VIP,        </li>
              <li>      </li>
              <li>    </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">3</div>
               
            </div>
            <p>' '      .</p>
            <ul className="checklist">
              <li>    /  </li>
              <li>    </li>
              <li>  </li>
            </ul>
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="card">
            <div className="card-title">Q.     ?</div>
            <p><strong>A.</strong> ,         .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.       ?</div>
            <p><strong>A.</strong> ,        .   '' 'A'     .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.     ?</div>
            <p><strong>A.</strong>       .        .</p>
          </div>
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>         .</p>
          
          <div className="grid-container">
            <div className="card">
              <div className="card-title">  </div>
              <p>support@mbaas.com</p>
              <p>24  </p>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default MessageGroupPage;