import React, { useState, useRef } from 'react';

function SendTalkPage() {
  //  
  const [selectedSender, setSelectedSender] = useState('');
  const [selectedRecipients, setSelectedRecipients] = useState([]);
  const [isRecipientModalOpen, setIsRecipientModalOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [activeTab, setActiveTab] = useState('groups');

  //   
  const [selectedTemplate, setSelectedTemplate] = useState('');
  const [talkType, setTalkType] = useState('basic'); //  
  const [emphasisType, setEmphasisType] = useState('none'); // 
  const [buttons, setButtons] = useState([]); //  

  //  
  const [content, setContent] = useState('');

  //  
  const groups = [
    { id: 1, name: '', count: 5 },
    { id: 2, name: '', count: 8 },
    { id: 3, name: '', count: 20 },
    { id: 4, name: 'VIP', count: 15 }
  ];

  const senderNumbers = [
    { id: 1, number: '02-1234-5678', name: '' },
    { id: 2, number: '070-1234-5678', name: '' },
    { id: 3, number: '010-1234-5678', name: '' }
  ];

  const members = [
    { id: 1, name: '', phone: '010-1234-5678', description: '', groupId: 1 },
    { id: 2, name: '', phone: '010-9876-5432', description: '', groupId: 2 },
    { id: 3, name: '', phone: '010-5555-1234', description: '', groupId: 2 },
    { id: 4, name: '', phone: '010-1111-2222', description: 'VIP', groupId: 4 },
    { id: 5, name: '', phone: '010-3333-4444', description: '', groupId: 1 }
  ];

  //    (SMS     )
  const approvedTemplates = [
    { 
      id: 1, 
      name: '  ', 
      category: '', 
      subCategory: '',
      content: ' #{},\n  .\n\n: #{}\n: #{}\n\n.',
      status: 'approved',
      talkType: 'basic',
      emphasisType: 'none'
    },
    { 
      id: 2, 
      name: '  ', 
      category: '', 
      subCategory: '',
      content: '#{}  .\n\n: #{}\n: #{}\n\n    .',
      status: 'approved',
      talkType: 'additional',
      emphasisType: 'title'
    },
    { 
      id: 3, 
      name: '  ', 
      category: '', 
      subCategory: '',
      content: '#{}   !\n\n#{}  \n #{}% .\n\n : #{} ~ #{}',
      status: 'approved',
      talkType: 'channel',
      emphasisType: 'image'
    }
  ];

  //   
  const talkTypes = [
    { value: 'basic', label: '', description: '    ' },
    { value: 'additional', label: '', description: '    ' },
    { value: 'channel', label: ' ', description: '    ' },
    { value: 'complex', label: '', description: '    ' }
  ];

  //  
  const emphasisTypes = [
    { value: 'none', label: '', description: '  ' },
    { value: 'title', label: '', description: '  ' },
    { value: 'image', label: '', description: '  ' },
    { value: 'itemlist', label: '', description: '   ' }
  ];

  //   
  const buttonTypes = [
    { value: 'weblink', label: '', needsUrl: true },
    { value: 'applink', label: '', needsUrl: true },
    { value: 'botkeyword', label: '', needsUrl: false },
    { value: 'message', label: ' ', needsUrl: false },
    { value: 'delivery', label: '', needsUrl: true },
    { value: 'consult', label: '', needsUrl: false },
    { value: 'bot', label: '', needsUrl: false }
  ];

  //    (SMS )
  const openRecipientModal = () => setIsRecipientModalOpen(true);
  const closeRecipientModal = () => setIsRecipientModalOpen(false);

  const toggleRecipient = (recipient) => {
    setSelectedRecipients(prev => {
      const exists = prev.find(r => r.id === recipient.id);
      if (exists) {
        return prev.filter(r => r.id !== recipient.id);
      } else {
        return [...prev, recipient];
      }
    });
  };

  const addGroupRecipients = (groupId) => {
    const groupMembers = members.filter(member => member.groupId === groupId);
    setSelectedRecipients(prev => {
      const newRecipients = [...prev];
      groupMembers.forEach(member => {
        if (!newRecipients.find(r => r.id === member.id)) {
          newRecipients.push(member);
        }
      });
      return newRecipients;
    });
  };

  const removeRecipient = (recipientId) => {
    setSelectedRecipients(prev => prev.filter(r => r.id !== recipientId));
  };

  //  
  const filteredGroups = groups.filter(group =>
    group.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const filteredMembers = members.filter(member =>
    member.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    member.phone.includes(searchQuery) ||
    member.description.toLowerCase().includes(searchQuery.toLowerCase())
  );

  //   
  const handleTemplateSelect = (templateId) => {
    const template = approvedTemplates.find(t => t.id === parseInt(templateId));
    if (template) {
      setSelectedTemplate(templateId);
      setContent(template.content);
      setTalkType(template.talkType);
      setEmphasisType(template.emphasisType);
    } else {
      setSelectedTemplate('');
      setContent('');
      setTalkType('basic');
      setEmphasisType('none');
    }
  };

  //  
  const addButton = () => {
    if (buttons.length < 5) { //  5 
      setButtons([...buttons, { 
        id: Date.now(), 
        type: 'weblink', 
        name: '', 
        url: '' 
      }]);
    }
  };

  //  
  const updateButton = (buttonId, field, value) => {
    setButtons(buttons.map(button => 
      button.id === buttonId ? { ...button, [field]: value } : button
    ));
  };

  //  
  const removeButton = (buttonId) => {
    setButtons(buttons.filter(button => button.id !== buttonId));
  };

  return (
    <>
      <section id="main-content">
        <h1> </h1>

        <p className="main-description">
                    .    ,      .
        </p>

        <div className="alert alert-info">
          <strong>  :</strong> 
                .        .
        </div>

        <h2 id="talk-overview"> </h2>
        <div className="card">
          <div className="card-title"> </div>
          
          <div className="grid-container">
            <div className="card feature-card">
              <div className="card-title">
                <span className="feature-icon"></span>
                 
              </div>
              <p><strong> :</strong>     </p>
              <p><strong> :</strong>     </p>
              <ul className="checklist">
                <li>    </li>
                <li>   </li>
                <li>  </li>
              </ul>
            </div>

            <div className="card feature-card">
              <div className="card-title">
                <span className="feature-icon"></span>
                 
              </div>
              <p><strong>:</strong> , ,  , </p>
              <p><strong>:</strong> , ,  </p>
              <ul className="checklist">
                <li>   </li>
                <li>  </li>
                <li>  </li>
              </ul>
            </div>

            <div className="card feature-card">
              <div className="card-title">
                <span className="feature-icon"></span>
                 
              </div>
              <p><strong> :</strong> , ,  </p>
              <p><strong>:</strong>  5  </p>
              <ul className="checklist">
                <li>   </li>
                <li>   </li>
                <li> </li>
              </ul>
            </div>
          </div>
        </div>

        <h2 id="send-settings"> </h2>
        <div className="card">
          <div className="card-title">    </div>
          
          <div style={{display: 'grid', gap: '20px'}}>
            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                  
              </label>
              
              <div style={{marginBottom: '12px'}}>
                <button 
                  className="btn btn-secondary"
                  onClick={openRecipientModal}
                  style={{width: '100%', justifyContent: 'space-between'}}
                >
                  <span>
                    {selectedRecipients.length > 0 
                      ? `${selectedRecipients.length} ` 
                      : '  '
                    }
                  </span>
                  <span></span>
                </button>
              </div>

              {selectedRecipients.length > 0 && (
                <div className="card" style={{background: 'var(--bg-secondary)', marginBottom: '16px'}}>
                  <div className="card-title" style={{fontSize: '1rem', marginBottom: '12px'}}>
                       ({selectedRecipients.length})
                  </div>
                  <div style={{display: 'flex', flexWrap: 'wrap', gap: '8px'}}>
                    {selectedRecipients.map(recipient => (
                      <div key={recipient.id} style={{
                        display: 'flex',
                        alignItems: 'center',
                        gap: '8px',
                        background: 'white',
                        padding: '6px 12px',
                        borderRadius: 'var(--radius-sm)',
                        fontSize: '0.9rem',
                        border: '1px solid var(--border-light)'
                      }}>
                        <span>{recipient.name} ({recipient.phone})</span>
                        <button
                          onClick={() => removeRecipient(recipient.id)}
                          style={{
                            background: 'none',
                            border: 'none',
                            color: 'var(--text-muted)',
                            cursor: 'pointer',
                            padding: '0',
                            fontSize: '0.8rem'
                          }}
                        >
                          
                        </button>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                 
              </label>
              <select
                value={selectedSender}
                onChange={(e) => setSelectedSender(e.target.value)}
                style={{
                  width: '100%',
                  padding: '12px',
                  border: '1px solid var(--border-color)',
                  borderRadius: 'var(--radius-md)',
                  fontSize: '14px'
                }}
              >
                <option value=""> </option>
                {senderNumbers.map(sender => (
                  <option key={sender.id} value={sender.id}>
                    {sender.number} ({sender.name})
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        <h2 id="template-selection">  </h2>
        <div className="card">
          <div className="card-title">    </div>
          <p>      .      .</p>
          
          <div style={{marginBottom: '20px'}}>
            <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                <span style={{color: 'red'}}>*</span>
            </label>
            <select
              value={selectedTemplate}
              onChange={(e) => handleTemplateSelect(e.target.value)}
              style={{
                width: '100%',
                padding: '12px',
                border: '1px solid var(--border-color)',
                borderRadius: 'var(--radius-md)',
                fontSize: '14px'
              }}
            >
              <option value="">  </option>
              {approvedTemplates.map(template => (
                <option key={template.id} value={template.id}>
                  [{template.status === 'approved' ? '' : ''}] {template.name} ({template.category})
                </option>
              ))}
            </select>
          </div>

          {selectedTemplate && (
            <div className="alert alert-success">
              <strong>   !</strong><br/>
                  ,   .
            </div>
          )}
        </div>

        <h2 id="talk-type-settings">  </h2>
        <div className="card">
          <div className="card-title">    </div>
          
          <div style={{display: 'grid', gap: '20px'}}>
            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                 
              </label>
              <div className="grid-container">
                {talkTypes.map(type => (
                  <label key={type.value} className="checkbox-item" style={{
                    flexDirection: 'column',
                    alignItems: 'flex-start',
                    padding: '16px',
                    border: talkType === type.value ? '2px solid var(--primary-color)' : '1px solid var(--border-color)',
                    borderRadius: 'var(--radius-md)',
                    cursor: 'pointer',
                    background: talkType === type.value ? 'rgba(59, 130, 246, 0.05)' : 'white'
                  }}>
                    <div style={{display: 'flex', alignItems: 'center', gap: '8px', marginBottom: '8px'}}>
                      <input
                        type="radio"
                        name="talkType"
                        value={type.value}
                        checked={talkType === type.value}
                        onChange={(e) => setTalkType(e.target.value)}
                      />
                      <strong>{type.label}</strong>
                    </div>
                    <div style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                      {type.description}
                    </div>
                  </label>
                ))}
              </div>
            </div>

            <div>
              <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                
              </label>
              <div className="grid-container">
                {emphasisTypes.map(type => (
                  <label key={type.value} className="checkbox-item" style={{
                    flexDirection: 'column',
                    alignItems: 'flex-start',
                    padding: '16px',
                    border: emphasisType === type.value ? '2px solid var(--primary-color)' : '1px solid var(--border-color)',
                    borderRadius: 'var(--radius-md)',
                    cursor: 'pointer',
                    background: emphasisType === type.value ? 'rgba(59, 130, 246, 0.05)' : 'white'
                  }}>
                    <div style={{display: 'flex', alignItems: 'center', gap: '8px', marginBottom: '8px'}}>
                      <input
                        type="radio"
                        name="emphasisType"
                        value={type.value}
                        checked={emphasisType === type.value}
                        onChange={(e) => setEmphasisType(e.target.value)}
                      />
                      <strong>{type.label}</strong>
                    </div>
                    <div style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                      {type.description}
                    </div>
                  </label>
                ))}
              </div>
            </div>
          </div>
        </div>

        <h2 id="message-content"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div style={{marginBottom: '16px'}}>
            <label style={{display: 'block', marginBottom: '8px', fontWeight: '600'}}>
                <span style={{color: 'var(--text-muted)', fontSize: '0.9rem'}}>(    )</span>
            </label>
            <textarea
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="     "
              rows="8"
              disabled={!selectedTemplate}
              style={{
                width: '100%',
                padding: '12px',
                border: '1px solid var(--border-color)',
                borderRadius: 'var(--radius-md)',
                fontSize: '14px',
                resize: 'vertical',
                backgroundColor: !selectedTemplate ? 'var(--bg-secondary)' : 'white'
              }}
            />
            
            {!selectedTemplate && (
              <div className="alert alert-warning" style={{marginTop: '8px'}}>
                <strong>   :</strong> 
                      .   .
              </div>
            )}
          </div>
        </div>

        <h2 id="button-settings"> </h2>
        <div className="card">
          <div className="card-title">
               ( 5)
            <button 
              className="btn btn-primary"
              onClick={addButton}
              disabled={buttons.length >= 5}
              style={{marginLeft: '12px', padding: '6px 12px', fontSize: '0.8rem'}}
            >
               
            </button>
          </div>
          
          {buttons.length === 0 ? (
            <div style={{
              textAlign: 'center',
              color: 'var(--text-muted)',
              padding: '40px 20px',
              background: 'var(--bg-secondary)',
              borderRadius: 'var(--radius-md)'
            }}>
                    
            </div>
          ) : (
            <div style={{display: 'grid', gap: '16px'}}>
              {buttons.map((button, index) => (
                <div key={button.id} className="card" style={{background: 'var(--bg-secondary)'}}>
                  <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '16px'}}>
                    <div className="card-title" style={{margin: 0, fontSize: '1rem'}}>
                       {index + 1}
                    </div>
                    <button
                      onClick={() => removeButton(button.id)}
                      style={{
                        background: 'none',
                        border: 'none',
                        color: 'var(--text-muted)',
                        cursor: 'pointer',
                        fontSize: '1.2rem'
                      }}
                    >
                      
                    </button>
                  </div>
                  
                  <div style={{display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '12px', marginBottom: '12px'}}>
                    <div>
                      <label style={{display: 'block', marginBottom: '4px', fontWeight: '600', fontSize: '0.9rem'}}>
                         
                      </label>
                      <select
                        value={button.type}
                        onChange={(e) => updateButton(button.id, 'type', e.target.value)}
                        style={{
                          width: '100%',
                          padding: '8px',
                          border: '1px solid var(--border-color)',
                          borderRadius: 'var(--radius-sm)',
                          fontSize: '0.9rem'
                        }}
                      >
                        {buttonTypes.map(type => (
                          <option key={type.value} value={type.value}>
                            {type.label}
                          </option>
                        ))}
                      </select>
                    </div>
                    
                    <div>
                      <label style={{display: 'block', marginBottom: '4px', fontWeight: '600', fontSize: '0.9rem'}}>
                        
                      </label>
                      <input
                        type="text"
                        value={button.name}
                        onChange={(e) => updateButton(button.id, 'name', e.target.value)}
                        placeholder="  "
                        style={{
                          width: '100%',
                          padding: '8px',
                          border: '1px solid var(--border-color)',
                          borderRadius: 'var(--radius-sm)',
                          fontSize: '0.9rem'
                        }}
                      />
                    </div>
                  </div>
                  
                  {buttonTypes.find(t => t.value === button.type)?.needsUrl && (
                    <div>
                      <label style={{display: 'block', marginBottom: '4px', fontWeight: '600', fontSize: '0.9rem'}}>
                         URL
                      </label>
                      <input
                        type="url"
                        value={button.url}
                        onChange={(e) => updateButton(button.id, 'url', e.target.value)}
                        placeholder="https://example.com"
                        style={{
                          width: '100%',
                          padding: '8px',
                          border: '1px solid var(--border-color)',
                          borderRadius: 'var(--radius-sm)',
                          fontSize: '0.9rem'
                        }}
                      />
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>

        <h2 id="send-preview"> </h2>
        <div className="card">
          <div className="card-title">   </div>
          
          <div className="card" style={{background: 'var(--bg-secondary)'}}>
            <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '16px'}}>
              <div className="card-title" style={{margin: 0}}></div>
              <span className="status-badge success"></span>
            </div>
            
            <div style={{
              background: 'white',
              padding: '20px',
              borderRadius: 'var(--radius-md)',
              border: '1px solid var(--border-color)',
              fontFamily: 'inherit'
            }}>
              <div style={{
                color: 'var(--text-primary)',
                lineHeight: '1.6',
                marginBottom: buttons.length > 0 ? '16px' : '0',
                whiteSpace: 'pre-line'
              }}>
                {content || '     .'}
              </div>
              
              {buttons.length > 0 && (
                <div style={{display: 'flex', flexDirection: 'column', gap: '8px'}}>
                  {buttons.map((button, index) => (
                    <div key={button.id} style={{
                      padding: '12px',
                      background: 'var(--bg-secondary)',
                      borderRadius: 'var(--radius-sm)',
                      textAlign: 'center',
                      fontSize: '0.9rem',
                      fontWeight: '500',
                      color: 'var(--primary-color)',
                      border: '1px solid var(--border-light)'
                    }}>
                      {button.name || ` ${index + 1}`}
                    </div>
                  ))}
                </div>
              )}
            </div>
            
            <div style={{marginTop: '12px', fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
              <div> : {selectedRecipients.length > 0 ? `${selectedRecipients.length}` : ''}</div>
              <div>: {selectedSender ? senderNumbers.find(s => s.id == selectedSender)?.number : ''}</div>
              <div> : {talkTypes.find(t => t.value === talkType)?.label}</div>
              <div>: {emphasisTypes.find(t => t.value === emphasisType)?.label}</div>
              <div> : {buttons.length}</div>
            </div>
          </div>
          
          <div style={{display: 'flex', gap: '12px', marginTop: '20px'}}>
            <button 
              className="btn btn-primary" 
              style={{flex: 1}}
              disabled={!selectedTemplate || selectedRecipients.length === 0}
            >
               
            </button>
            {/* <button className="btn btn-secondary">
               
            </button>
            <button className="btn btn-secondary">
              
            </button> */}
          </div>
        </div>

        <h2 id="usage-guide"> </h2>
        <div className="card">
          <div className="card-title">   </div>
          
          <div className="step-item">
            <div className="step-title">
              <div className="step-number">1</div>
               
            </div>
            <p>    .</p>
            <ul className="checklist">
              <li>      </li>
              <li>     </li>
              <li>       </li>
              <li>   (,  )</li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">2</div>
                
            </div>
            <p>     .</p>
            <ul className="checklist">
              <li>    </li>
              <li>      </li>
              <li>      </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">3</div>
                  
            </div>
            <p>    .</p>
            <ul className="checklist">
              <li>, ,  ,   </li>
              <li>: , ,  </li>
              <li>     </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">4</div>
                ()
            </div>
            <p>    .</p>
            <ul className="checklist">
              <li> 5   </li>
              <li>, ,    </li>
              <li>  URL </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">5</div>
                 
            </div>
            <p>    .</p>
            <ul className="checklist">
              <li>   </li>
              <li>    </li>
              <li>     </li>
            </ul>
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="card">
            <div className="card-title">Q.   .</div>
            <p><strong>A.</strong>     .        .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.     ?</div>
            <p><strong>A.</strong>      .         .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.   .</div>
            <p><strong>A.</strong>      URL  . URL http://  https://  .</p>
          </div>
          
          <div className="card">
            <div className="card-title">Q.  SMS  ?</div>
            <p><strong>A.</strong>       ,    . SMS        .</p>
          </div>
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>       .</p>
          
          <div className="grid-container">
            <div className="card">
              <div className="card-title">  </div>
              <p>support@mbaas.com</p>
              <p>24  </p>
            </div>
          </div>
        </div>

        {/*     (SMS ) */}
        {isRecipientModalOpen && (
          <div style={{
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            zIndex: 1000
          }}>
            <div style={{
              backgroundColor: 'white',
              borderRadius: 'var(--radius-lg)',
              width: '90%',
              maxWidth: '1000px',
              height: '80%',
              maxHeight: '700px',
              display: 'flex',
              flexDirection: 'column',
              boxShadow: 'var(--shadow-lg)'
            }}>
              <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                padding: '20px',
                borderBottom: '1px solid var(--border-color)'
              }}>
                <h3 style={{margin: 0, color: 'var(--text-primary)'}}>  </h3>
                <button
                  onClick={closeRecipientModal}
                  style={{
                    background: 'none',
                    border: 'none',
                    fontSize: '1.5rem',
                    cursor: 'pointer',
                    color: 'var(--text-muted)'
                  }}
                >
                  
                </button>
              </div>

              <div style={{
                display: 'flex',
                flex: 1,
                overflow: 'hidden'
              }}>
                <div style={{
                  flex: 1,
                  padding: '20px',
                  borderRight: '1px solid var(--border-color)',
                  overflow: 'auto',
                  display: 'flex',
                  flexDirection: 'column'
                }}>
                  <h4 style={{marginTop: 0, color: 'var(--text-primary)'}}>  </h4>
                  
                  <div style={{marginBottom: '20px'}}>
                    <input
                      type="text"
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      placeholder=", , ,  ..."
                      style={{
                        width: '100%',
                        padding: '12px',
                        border: '1px solid var(--border-color)',
                        borderRadius: 'var(--radius-md)',
                        fontSize: '14px'
                      }}
                    />
                  </div>

                  <div style={{
                    display: 'flex',
                    marginBottom: '20px',
                    background: 'var(--bg-secondary)',
                    borderRadius: 'var(--radius-md)',
                    padding: '4px'
                  }}>
                    <button
                      onClick={() => setActiveTab('groups')}
                      style={{
                        flex: 1,
                        padding: '8px 16px',
                        border: 'none',
                        borderRadius: 'var(--radius-sm)',
                        background: activeTab === 'groups' ? 'var(--primary-color)' : 'transparent',
                        color: activeTab === 'groups' ? 'white' : 'var(--text-secondary)',
                        cursor: 'pointer',
                        fontSize: '0.9rem',
                        fontWeight: '500',
                        transition: 'all 0.2s'
                      }}
                    >
                       ({filteredGroups.length})
                    </button>
                    <button
                      onClick={() => setActiveTab('members')}
                      style={{
                        flex: 1,
                        padding: '8px 16px',
                        border: 'none',
                        borderRadius: 'var(--radius-sm)',
                        background: activeTab === 'members' ? 'var(--primary-color)' : 'transparent',
                        color: activeTab === 'members' ? 'white' : 'var(--text-secondary)',
                        cursor: 'pointer',
                        fontSize: '0.9rem',
                        fontWeight: '500',
                        transition: 'all 0.2s'
                      }}
                    >
                       ({filteredMembers.length})
                    </button>
                  </div>

                  <div style={{flex: 1, overflow: 'auto'}}>
                    {activeTab === 'groups' && (
                      <div>
                        {filteredGroups.length === 0 ? (
                          <div style={{
                            textAlign: 'center',
                            color: 'var(--text-muted)',
                            padding: '40px 20px'
                          }}>
                            {searchQuery ? '  ' : ' '}
                          </div>
                        ) : (
                          filteredGroups.map(group => (
                            <div key={group.id} style={{
                              display: 'flex',
                              justifyContent: 'space-between',
                              alignItems: 'center',
                              padding: '12px',
                              background: 'var(--bg-secondary)',
                              borderRadius: 'var(--radius-md)',
                              marginBottom: '8px'
                            }}>
                              <div>
                                <strong>{group.name}</strong>
                                <span style={{color: 'var(--text-secondary)', marginLeft: '8px'}}>
                                  ({group.count})
                                </span>
                              </div>
                              <button
                                className="btn btn-primary"
                                style={{padding: '6px 12px', fontSize: '0.8rem'}}
                                onClick={() => addGroupRecipients(group.id)}
                              >
                                 
                              </button>
                            </div>
                          ))
                        )}
                      </div>
                    )}

                    {activeTab === 'members' && (
                      <div>
                        {filteredMembers.length === 0 ? (
                          <div style={{
                            textAlign: 'center',
                            color: 'var(--text-muted)',
                            padding: '40px 20px'
                          }}>
                            {searchQuery ? '  ' : ' '}
                          </div>
                        ) : (
                          filteredMembers.map(member => (
                            <div key={member.id} style={{
                              display: 'flex',
                              justifyContent: 'space-between',
                              alignItems: 'center',
                              padding: '12px',
                              background: selectedRecipients.find(r => r.id === member.id) 
                                ? 'rgba(59, 130, 246, 0.1)' 
                                : 'var(--bg-secondary)',
                              borderRadius: 'var(--radius-md)',
                              marginBottom: '8px',
                              border: selectedRecipients.find(r => r.id === member.id) 
                                ? '1px solid var(--primary-color)' 
                                : '1px solid var(--border-light)'
                            }}>
                              <div>
                                <div><strong>{member.name}</strong></div>
                                <div style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                                  {member.phone} | {member.description}
                                </div>
                              </div>
                              <button
                                className={`btn ${selectedRecipients.find(r => r.id === member.id) ? 'btn-primary' : 'btn-secondary'}`}
                                style={{padding: '6px 12px', fontSize: '0.8rem'}}
                                onClick={() => toggleRecipient(member)}
                              >
                                {selectedRecipients.find(r => r.id === member.id) ? '' : ''}
                              </button>
                            </div>
                          ))
                        )}
                      </div>
                    )}
                  </div>
                </div>

                <div style={{
                  flex: 1,
                  padding: '20px',
                  overflow: 'auto'
                }}>
                  <h4 style={{marginTop: 0, color: 'var(--text-primary)'}}>
                       ({selectedRecipients.length})
                  </h4>
                  
                  {selectedRecipients.length === 0 ? (
                    <div style={{
                      textAlign: 'center',
                      color: 'var(--text-muted)',
                      padding: '40px 20px'
                    }}>
                        
                    </div>
                  ) : (
                    <div>
                      {selectedRecipients.map(recipient => (
                        <div key={recipient.id} style={{
                          display: 'flex',
                          justifyContent: 'space-between',
                          alignItems: 'center',
                          padding: '12px',
                          background: 'var(--bg-primary)',
                          border: '1px solid var(--border-color)',
                          borderRadius: 'var(--radius-md)',
                          marginBottom: '8px'
                        }}>
                          <div>
                            <div><strong>{recipient.name}</strong></div>
                            <div style={{fontSize: '0.9rem', color: 'var(--text-secondary)'}}>
                              {recipient.phone} | {recipient.description}
                            </div>
                          </div>
                          <button
                            onClick={() => removeRecipient(recipient.id)}
                            style={{
                              background: 'none',
                              border: 'none',
                              color: 'var(--text-muted)',
                              cursor: 'pointer',
                              padding: '4px',
                              borderRadius: '4px',
                              fontSize: '1.2rem'
                            }}
                          >
                            
                          </button>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              </div>

              <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                padding: '20px',
                borderTop: '1px solid var(--border-color)',
                background: 'var(--bg-secondary)'
              }}>
                <div style={{color: 'var(--text-secondary)'}}>
                   {selectedRecipients.length} 
                </div>
                <div style={{display: 'flex', gap: '12px'}}>
                  <button className="btn btn-secondary" onClick={closeRecipientModal}>
                    
                  </button>
                  <button className="btn btn-primary" onClick={closeRecipientModal}>
                      ({selectedRecipients.length})
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </section>
    </>
  );
}

export default SendTalkPage;