import React, { useState } from 'react';

function DefaultPage({ title, description }) {
  const [userType, setUserType] = useState('user'); // 'user'  'developer'

  return (
    <>
      <section id="main-content">
        <h1>{title}</h1>

        {/*   */}
        <p className="main-description"></p>

        <div className="alert alert-info">
          <strong>   :</strong> 
           '       .'
        </div>

        <h2 id="overview"></h2>
        <div className="card">
          <div className="card-title"> </div>
          <p>{userType === 'developer' ? ' ' : ' '} {title}      .</p>
          
          <div className="grid-container">
              <div>
                <div className="card-title">
                  <span className="feature-icon"></span>
                  
                </div>
                <p></p>
              </div>
          </div>
        </div>

        <h2 id="resources"> </h2>
        <div className="card">
          <div className="card-title">
          </div>
          <p>      .</p>
          
          <div className="resources-grid">
              <div className="resource-item">
                <div className="resource-title"></div>
                <div className="resource-desc"></div>
                <button className="btn btn-secondary"></button>
              </div>
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">
            {userType === 'developer' ? '  ' : '  '}
          </div>
          <div className="card">
            <div className="card-title">Q.    .</div>
            <p><strong>A.</strong>     .        .</p>
          </div>
          <div className="card">
            <div className="card-title">Q.   .</div>
            <p><strong>A.</strong>      .     .</p>
          </div>
          <div className="card">
            <div className="card-title">Q.   .</div>
            <p><strong>A.</strong>      .    ,   .</p>
          </div>
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>       .</p>
          
          <div className="grid-container">
            <div className="card">
              <div className="card-title">  </div>
              <p>support@mbaas.com</p>
              <p>24  </p>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default DefaultPage;