import React, { useState } from 'react';

function MessageTemplatesPage() {
  const [activeTab, setActiveTab] = useState('sms'); // 'sms'  'talk'
  const [smsTemplates, setSmsTemplates] = useState([
    {
      id: 1,
      name: '  ',
      category: '',
      content: ' ${name}!  .    .',
      type: 'SMS',
      variables: ['name'],
      createdAt: '2024-01-15',
      usageCount: 1250
    },
    {
      id: 2,
      name: '  ',
      category: '',
      content: ' . : ${orderNo}\n : ${amount}\n 2-3 .',
      type: 'LMS',
      variables: ['orderNo', 'amount'],
      createdAt: '2024-01-10',
      usageCount: 3400
    }
  ]);
  
  const [talkTemplates, setTalkTemplates] = useState([
    {
      id: 1,
      name: '  ',
      category: '',
      templateCode: 'EVENT_001',
      content: '${name},   !\n ${date} .',
      talkType: '',
      emphasisType: '',
      variables: ['name', 'date'],
      buttons: [
        { type: 'weblink', name: ' ', url: 'https://example.com/event' }
      ],
      status: '',
      createdAt: '2024-01-12',
      usageCount: 850
    },
    {
      id: 2,
      name: '  ',
      category: '',
      templateCode: 'DELIVERY_001',
      content: '   .\n: ${trackingNo}',
      talkType: '',
      emphasisType: '',
      variables: ['trackingNo'],
      buttons: [
        { type: 'weblink', name: '', url: 'https://example.com/tracking' },
        { type: 'applink', name: ' ', url: 'myapp://tracking' }
      ],
      status: '',
      createdAt: '2024-01-08',
      usageCount: 2100
    }
  ]);

  const [selectedTemplate, setSelectedTemplate] = useState(null);
  const [isEditing, setIsEditing] = useState(false);
  const [isCreating, setIsCreating] = useState(false);
  const [newTemplate, setNewTemplate] = useState({
    name: '',
    category: '',
    content: '',
    variables: [],
    templateCode: '',
    talkType: '',
    emphasisType: '',
    buttons: []
  });

  const startEdit = (template) => {
    setSelectedTemplate(template);
    setIsEditing(true);
    setIsCreating(false);
  };

  const startCreate = () => {
    setNewTemplate({
      name: '',
      category: '',
      content: '',
      variables: [],
      templateCode: '',
      talkType: '',
      emphasisType: '',
      buttons: []
    });
    setIsCreating(true);
    setIsEditing(false);
  };

  const cancelEdit = () => {
    setIsEditing(false);
    setIsCreating(false);
    setSelectedTemplate(null);
  };

  const deleteTemplate = (id) => {
    if (window.confirm('   ?')) {
      if (activeTab === 'sms') {
        setSmsTemplates(smsTemplates.filter(t => t.id !== id));
      } else {
        setTalkTemplates(talkTemplates.filter(t => t.id !== id));
      }
    }
  };

  const extractVariables = (content) => {
    const matches = content.match(/\$\{[^}]+\}/g);
    return matches ? matches.map(match => match.slice(2, -1)) : [];
  };

  const renderTemplateList = () => {
    const templates = activeTab === 'sms' ? smsTemplates : talkTemplates;
    
    return (
      <div className="api-table">
        <table style={{width: '100%'}}>
          <thead>
            <tr>
              <th> </th>
              <th></th>
              {activeTab === 'talk' && <th></th>}
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {templates.map(template => (
              <tr key={template.id}>
                <td><strong>{template.name}</strong></td>
                <td>{template.category}</td>
                {activeTab === 'talk' && (
                  <td>
                    <span style={{
                      padding: '4px 8px',
                      borderRadius: '12px',
                      fontSize: '0.8rem',
                      background: template.status === '' ? 'var(--success-bg)' : 'var(--warning-bg)',
                      color: template.status === '' ? 'var(--success-color)' : 'var(--warning-color)'
                    }}>
                      {template.status}
                    </span>
                  </td>
                )}
                <td>{template.usageCount.toLocaleString()}</td>
                <td>{template.createdAt}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  };

  return (
    <>
      <section id="main-content">
        <h1>  </h1>

        <p className="main-description">
                   . SMS/MMS      .
        </p>

        <div className="alert alert-info">
          <strong>   :</strong> 
                  . ${`{}`}  .
        </div>

        <h2 id="template-management"> </h2>
        <div className="card">
          <div style={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginBottom: '20px'
          }}>
            <div style={{
              display: 'flex',
              background: 'var(--bg-secondary)',
              borderRadius: 'var(--radius-md)',
              padding: '4px'
            }}>
              <button
                onClick={() => setActiveTab('sms')}
                style={{
                  flex: 1,
                  padding: '8px 20px',
                  border: 'none',
                  borderRadius: 'var(--radius-sm)',
                  background: activeTab === 'sms' ? 'var(--primary-color)' : 'transparent',
                  color: activeTab === 'sms' ? 'white' : 'var(--text-secondary)',
                  cursor: 'pointer',
                  fontSize: '0.9rem',
                  fontWeight: '500',
                  transition: 'all 0.2s'
                }}
              >
                SMS/MMS 
              </button>
              <button
                onClick={() => setActiveTab('talk')}
                style={{
                  flex: 1,
                  padding: '8px 20px',
                  border: 'none',
                  borderRadius: 'var(--radius-sm)',
                  background: activeTab === 'talk' ? 'var(--primary-color)' : 'transparent',
                  color: activeTab === 'talk' ? 'white' : 'var(--text-secondary)',
                  cursor: 'pointer',
                  fontSize: '0.9rem',
                  fontWeight: '500',
                  transition: 'all 0.2s'
                }}
              >
                 
              </button>
            </div>
          </div>

          {renderTemplateList()}
        </div>

        <h2 id="template-usage">  </h2>
        <div className="card">
          <div className="card-title">   </div>
          
          <div className="grid-container">
            <div className="card">
              <div className="card-title">
                <span style={{color: 'var(--primary-color)', marginRight: '8px'}}></span>
                 
              </div>
              <p>   <code className='field-name'>${`{}`}</code>   .</p>
              <div className="code-example">
                <strong>:</strong><br/>
                 <code className='field-name'>${`{name}`}</code>!<br/>
                 <code className='field-name'>${`{orderNo}`}</code> .
              </div>
            </div>

            <div className="card">
              <div className="card-title">
                <span style={{color: 'var(--success-color)', marginRight: '8px'}}></span>
                SMS/LMS  
              </div>
              <p>SMS    80   LMS .</p>
              <ul className="checklist">
                <li> 1 = 2</li>
                <li>/ 1 = 1</li>
                <li>80   LMS (2,000 )</li>
              </ul>
            </div>

            <div className="card">
              <div className="card-title">
                <span style={{color: 'var(--warning-color)', marginRight: '8px'}}></span>
                  
              </div>
              <p>      .</p>
              <ul className="checklist">
                <li>     (1-2 )</li>
                <li>    </li>
                <li>     </li>
              </ul>
            </div>
          </div>
        </div>

        <h2 id="usage-guide"> </h2>
        <div className="card">
          <div className="card-title">   </div>
          
          <div className="step-item">
            <div className="step-title">
              <div className="step-number">1</div>
                
            </div>
            <p>SMS/MMS       .</p>
            <ul className="checklist">
              <li><strong>SMS/MMS:</strong>   ,   </li>
              <li><strong>:</strong>  ,    </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">2</div>
               
            </div>
            <p>'  '    .</p>
            <ul className="checklist">
              <li>    </li>
              <li>    (: ${`{name}, {orderNo}`})</li>
              <li>     </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">3</div>
               
            </div>
            <p>     .</p>
            <ul className="checklist">
              <li>     </li>
              <li>  </li>
              <li>   </li>
            </ul>
          </div>

          <div className="step-item">
            <div className="step-title">
              <div className="step-number">4</div>
                 
            </div>
            <p>SMS/MMS         .</p>
            <ul className="checklist">
              <li>     </li>
              <li>    </li>
              <li>    </li>
            </ul>
          </div>
        </div>

        <h2 id="variables-guide">  </h2>
        <div className="card">
          <div className="card-title">   </div>
          
          <div className="api-table">
            <table style={{width: '100%'}}>
              <thead>
                <tr>
                  <th></th>
                  <th></th>
                  <th> </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td><code className='field-name'>name</code></td>
                  <td> </td>
                  <td> <code className='field-name'>${`{name}`}</code>!</td>
                </tr>
                <tr>
                  <td><code className='field-name'>orderNo</code></td>
                  <td></td>
                  <td>: <code className='field-name'>${`{orderNo}`}</code></td>
                </tr>
                <tr>
                  <td><code className='field-name'>amount</code></td>
                  <td></td>
                  <td>: <code className='field-name'>${`{amount}`}</code></td>
                </tr>
                <tr>
                  <td><code className='field-name'>date</code></td>
                  <td></td>
                  <td><code className='field-name'>${`{date}`}</code> </td>
                </tr>
                <tr>
                  <td><code className='field-name'>trackingNo</code></td>
                  <td></td>
                  <td>: <code className='field-name'>${`{trackingNo}`}</code></td>
                </tr>
                <tr>
                  <td><code className='field-name'>phone</code></td>
                  <td></td>
                  <td>: <code className='field-name'>${`{phone}`}</code></td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="card">
            <div className="card-title">Q.   .</div>
            <p><strong>A.</strong>   :</p>
            <ul className="checklist">
              <li>   </li>
              <li>   </li>
              <li>   </li>
            </ul>
          </div>
          
          <div className="card">
            <div className="card-title">Q.    .</div>
            <p><strong>A.</strong>   :</p>
            <ul className="checklist">
              <li> : <code className='field-name'>${`{}`}</code></li>
              <li> : <code className='field-name'>${`{}, <>, []`}</code></li>
              <li>    </li>
            </ul>
          </div>
          
          <div className="card">
            <div className="card-title">Q. SMS LMS   .</div>
            <p><strong>A.</strong>    :</p>
            <ul className="checklist">
              <li>, : 2</li>
              <li>, : 1</li>
              <li>80   LMS  </li>
            </ul>
          </div>
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>       .</p>
          
          <div className="grid-container">
            <div className="card">
              <div className="card-title">  </div>
              <p>support@mbaas.com</p>
              <p>24  </p>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default MessageTemplatesPage;