import React, { useState } from 'react';

function DefaultPage({ title, description }) {
  const [userType, setUserType] = useState('user'); // 'user'  'developer'

  //    
  const getContentByUserType = () => {
    if (userType === 'developer') {
      return {
        description: description || '    API  .',
        features: [
          {
            icon: '',
            title: 'API ',
            description: 'RESTful API   .',
            buttonText: 'API ',
            priority: 'high'
          },
          {
            icon: '',
            title: 'SDK ',
            description: '  SDK    .',
            buttonText: 'SDK ',
            priority: 'high'
          },
          {
            icon: '',
            title: ' ',
            description: '     .',
            buttonText: '',
            priority: 'medium'
          }
        ],
        quickStart: {
          title: '  ',
          steps: [
            '  ',
            'API   ',
            'SDK   ',
            'Hello World  '
          ]
        },
        resources: [
          { title: '  ', description: '    ' },
          { title: '  ', description: '    ' },
          { title: '  ', description: '  ' }
        ]
      };
    } else {
      return {
        description: description || '       .',
        features: [
          {
            icon: '',
            title: ' ',
            description: '       .',
            buttonText: '',
            priority: 'high'
          },
          {
            icon: '',
            title: ' ',
            description: '    .',
            buttonText: '',
            priority: 'high'
          },
          {
            icon: '',
            title: ' ',
            description: '     .',
            buttonText: '',
            priority: 'medium'
          }
        ],
        quickStart: {
          title: '  ',
          steps: [
            '   ',
            ' ',
            '  ',
            '   '
          ]
        },
        resources: [
          { title: '  ', description: '   ' },
          { title: ' FAQ', description: '   ' },
          { title: ' ', description: '   ' }
        ]
      };
    }
  };

  const content = getContentByUserType();

  return (
    <>
      <section id="main-content">
        <h1>{title}</h1>
        
        {/*      */}
        <div className="user-type-selector">
          <div className="selector-header">
            <h3>  ?</h3>
            <p>     .</p>
          </div>
          <div className="type-buttons">
            <button 
              className={`type-btn ${userType === 'user' ? 'active' : ''}`}
              onClick={() => setUserType('user')}
            >
              <div className="type-icon"></div>
              <div className="type-info">
                <div className="type-title"> </div>
                <div className="type-desc">,  </div>
              </div>
            </button>
            
            <button 
              className={`type-btn ${userType === 'developer' ? 'active' : ''}`}
              onClick={() => setUserType('developer')}
            >
              <div className="type-icon"></div>
              <div className="type-info">
                <div className="type-title"></div>
                <div className="type-desc">,  </div>
              </div>
            </button>
          </div>
        </div>

        {/*   */}
        <p className="main-description">{content.description}</p>

        <div className="alert alert-info">
          <strong> {userType === 'developer' ? ' ' : ' '}:</strong> 
          {userType === 'developer' 
            ? '  API     .'
            : '       .'
          }
        </div>

        <h2 id="overview"></h2>
        <div className="card">
          <div className="card-title"> </div>
          <p>{userType === 'developer' ? ' ' : ' '} {title}      .</p>
          
          <div className="grid-container">
            {content.features.map((feature, index) => (
              <div key={index} className={`card feature-card ${feature.priority}`}>
                <div className="card-title">
                  <span className="feature-icon">{feature.icon}</span>
                  {feature.title}
                  {feature.priority === 'high' && <span className="priority-badge"></span>}
                </div>
                <p>{feature.description}</p>
                <button className="btn btn-primary">{feature.buttonText}</button>
              </div>
            ))}
          </div>
        </div>

        <h2 id="getting-started">{content.quickStart.title}</h2>
        <div className="card">
          <div className="card-title">
            {userType === 'developer' ? '  ' : ' '}
          </div>
          <p>{title}     .</p>
          
          <div className="step-content">
            <h3 id="step-1"> </h3>
            <ul className="checklist">
              {content.quickStart.steps.map((step, index) => (
                <li key={index}>{step}</li>
              ))}
            </ul>
          </div>

          {userType === 'developer' && (
            <div className="code-example">
              <h4>  </h4>
              <div className="code-block">
                <pre><code>{`// ${title}   
import { mBaasSDK } from '@mbaas/sdk';

const client = new mBaasSDK({
  apiKey: 'your-api-key',
  projectId: 'your-project-id'
});

//   
await client.${title.toLowerCase().replace(/\s+/g, '')}.initialize();`}</code></pre>
              </div>
            </div>
          )}
        </div>

        <h2 id="resources"> </h2>
        <div className="card">
          <div className="card-title">
            {userType === 'developer' ? ' ' : ' '}
          </div>
          <p>      .</p>
          
          <div className="resources-grid">
            {content.resources.map((resource, index) => (
              <div key={index} className="resource-item">
                <div className="resource-title">{resource.title}</div>
                <div className="resource-desc">{resource.description}</div>
                <button className="btn btn-secondary"></button>
              </div>
            ))}
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">
            {userType === 'developer' ? '  ' : '  '}
          </div>
          
          {userType === 'developer' ? (
            <>
              <div className="card">
                <div className="card-title">Q. API  .</div>
                <p><strong>A.</strong> API   URL  .   Content-Type    .</p>
              </div>
              <div className="card">
                <div className="card-title">Q. SDK   .</div>
                <p><strong>A.</strong>  ID API   ,   SDK   .</p>
              </div>
              <div className="card">
                <div className="card-title">Q.   .</div>
                <p><strong>A.</strong> refresh token     ,     .</p>
              </div>
            </>
          ) : (
            <>
              <div className="card">
                <div className="card-title">Q.    .</div>
                <p><strong>A.</strong>     .        .</p>
              </div>
              <div className="card">
                <div className="card-title">Q.   .</div>
                <p><strong>A.</strong>      .     .</p>
              </div>
              <div className="card">
                <div className="card-title">Q.   .</div>
                <p><strong>A.</strong>      .    ,   .</p>
              </div>
            </>
          )}
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>       .</p>
          
          <div className="grid-container">
            {userType === 'developer' ? (
              <>
                <div className="card">
                  <div className="card-title">  </div>
                  <p>dev-support@mbaas.com</p>
                  <p>   </p>
                </div>
                <div className="card">
                  <div className="card-title">  </div>
                  <p>developers.mbaas.com</p>
                  <p>  </p>
                </div>
                <div className="card">
                  <div className="card-title">  </div>
                  <p>GitHub Issues</p>
                  <p>    </p>
                </div>
              </>
            ) : (
              <>
                <div className="card">
                  <div className="card-title">  </div>
                  <p>02-1234-5678</p>
                  <p> 9:00 - 18:00</p>
                </div>
                <div className="card">
                  <div className="card-title">  </div>
                  <p>support@mbaas.com</p>
                  <p>24  </p>
                </div>
                <div className="card">
                  <div className="card-title">  </div>
                  <p>  </p>
                  <p> 9:00 - 18:00</p>
                </div>
              </>
            )}
          </div>
        </div>
      </section>
    </>
  );
}

export default DefaultPage;