import React, { useState } from 'react';

function CommentModeration({ title = " ", description }) {
  const [userType, setUserType] = useState('admin'); // 'admin'  'moderator'
  
  //    
  const getContentByUserType = () => {
    if (userType === 'moderator') {
      return {
        description: description || '   ,  ,       .',
        features: [
          {
            icon: '',
            title: ' ',
            description: '    / .',
            priority: 'high'
          },
          {
            icon: '',
            title: '  ',
            description: ', ,    .',
            priority: 'high'
          },
          {
            icon: '',
            title: ' ',
            description: '    .',
            priority: 'medium'
          }
        ],
        quickStart: {
          title: '  ',
          steps: [
            '  ',
            '   ',
            '    ',
            '    '
          ]
        }
      };
    } else {
      return {
        description: description || '      .',
        features: [
          {
            icon: '',
            title: '  ',
            description: '     .',
            buttonText: '',
            priority: 'high'
          },
          {
            icon: '',
            title: ' ',
            description: '     .',
            buttonText: '',
            priority: 'medium'
          }
        ],
        quickStart: {
          title: '  ',
          steps: [
            '  ',
            '    ',
            '  ',
          ]
        }
      };
    }
  };

  const content = getContentByUserType();

  return (
    <>
      <section id="main-content">
        <h1>{title}</h1>

        <p className="main-description">
          {description || '      .'}
        </p>

        <div className="alert alert-info">
          <strong>  :</strong> 
                  .
        </div>

        <h2 id="overview"></h2>
        <div className="card">
          <div className="card-title"> </div>
          <p>{userType === 'admin' ? ' ' : ' '}        .</p>
          
          <div className="grid-container">
            {content.features.map((feature, index) => (
              <div key={index} className={`card feature-card ${feature.priority}`}>
                <div className="card-title">
                  <span className="feature-icon">{feature.icon}</span>
                  {feature.title}
                  {feature.priority === 'high' && <span className="priority-badge"></span>}
                </div>
                <p>{feature.description}</p>
              </div>
            ))}
          </div>
        </div>

        <h2 id="getting-started">{content.quickStart.title}</h2>
        <div className="card">
          <div className="card-title"> </div>
          <p>      .</p>
          
          <div className="step-content">
            <h3 id="step-1"> </h3>
            <ul className="checklist">
              {content.quickStart.steps.map((step, index) => (
                <li key={index}>{step}</li>
              ))}
            </ul>
          </div>
        </div>

        {/*    */}
        <h2 id="comment-system" className="comment-section-title">  </h2>
        <div className="card comment-management-card">
          <div className="card-title">   </div>
          <p>       .</p>
          
          <div className="step-content">
            <h3> </h3>
            <div className="step-detail">
              <div className="step-content">
                <div className="step-item">
                  <div className="step-title">
                    <span className="step-number">1</span>
                     
                  </div>
                  <div className="step-content">
                    <p>   .</p>
                  </div>
                </div>
                <div className="step-item">
                  <div className="step-title">
                    <span className="step-number">2</span>
                     
                  </div>
                  <div className="step-content">
                    <p>  .</p>
                  </div>
                </div>
                <div className="step-item">
                  <div className="step-title">
                    <span className="step-number">3</span>
                    /
                  </div>
                  <div className="step-content">
                    <p>  .</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/*    */}
        {userType === 'admin' && (
          <>
            <h2 id="system-settings" className="comment-section-title">  </h2>
            <div className="card comment-management-card">
              <div className="card-title">   </div>
              <p>      .</p>
              
              <div className="step-content">
                <h3>  </h3>
                <div className="step-detail">
                  <div className="grid-container">
                    <div className="card">
                      <div className="card-title">  </div>
                      <ul className="checklist">
                        <li><strong>  :</strong> , ,   </li>
                        <li><strong> :</strong>      </li>
                        <li><strong>/ :</strong>     </li>
                      </ul>
                    </div>
                    
                    <div className="card">
                      <div className="card-title">  </div>
                      <ul className="checklist">
                        <li><strong> :</strong> ,    </li>
                        <li><strong> :</strong> @  </li>
                      </ul>
                    </div>
                  </div>
                </div>

                {/* <h3>  </h3>
                <div className="step-detail">
                  <div className="alert alert-warning">
                    <strong> :</strong>         .    .
                  </div>
                  
                  <ul className="checklist">
                    <li><strong> :</strong>      </li>
                    <li><strong> :</strong>     </li>
                    <li><strong> :</strong>       </li>
                    <li><strong> :</strong>      </li>
                    <li><strong> :</strong>    </li>
                  </ul>
                </div> */}

                <h3>   </h3>
                <div className="step-detail">
                  <ul className="checklist">
                    <li><strong> :</strong>       </li>
                    <li><strong> :</strong>      </li>
                    <li><strong> :</strong>     </li>
                  </ul>
                </div>
              </div>
            </div>
          </>
        )}

        {/*     */}
        <h2 id="best-practices">   </h2>
        <div className="card">
          <div className="card-title">   </div>
          
          <h3 id="community-guidelines">   </h3>
          <div className="grid-container">
            <div className="card">
              <div className="card-title"> </div>
              <ul>
                <li>-     </li>
                <li>-   </li>
                <li>-   </li>
                <li>-   </li>
              </ul>
            </div>
            
            <div className="card">
              <div className="card-title"> </div>
              <ul>
                <li>-   </li>
                <li>-   </li>
                <li>-   </li>
                <li>-   </li>
              </ul>
            </div>
          </div>

          <h3 id="positive-engagement">   </h3>
          <ul className="checklist">
            <li><strong> :</strong>      </li>
            <li><strong>  :</strong>      </li>
            <li><strong> :</strong>  
                </li>
          </ul>

          <h3 id="continuous-improvement">  </h3>
          <ul className="checklist">
            <li><strong>  :</strong>  1     </li>
            <li><strong>  :</strong>      </li>
            <li><strong>  :</strong>     </li>
          </ul>

          <div className="alert alert-success">
            <strong>    :</strong>   ,             .
          </div>
        </div>

        <h2 id="troubleshooting"> </h2>
        <div className="card">
          <div className="card-title">  </div>
          
          <div className="card">
            <div className="card-title">Q.    .</div>
            <p><strong>A.</strong>    .        .       .</p>
          </div>
          <div className="card">
            <div className="card-title">Q.     .</div>
            <p><strong>A.</strong>    ,   .         .</p>
          </div>
        </div>

        <h2 id="support"> </h2>
        <div className="card">
          <div className="card-title">  ?</div>
          <p>       .</p>
          <div className="grid-container">
            <div className="card">
              <div className="card-title">  </div>
              <p>support@mbaas.com</p>
              <p>24  </p>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}

export default CommentModeration;