import React, { useState, useEffect } from 'react';
import './styles/variables.css';
import './styles/common.css';
import './styles/components/header.css';
import './styles/components/sidebar.css';
import './styles/components/TOC.css';

//  import  
import Header from './components/layout/Header';
import Sidebar from './components/layout/Sidebar';
import MainContent from './components/layout/MainContent';
import TOC from './components/layout/TOC';
import MobileMenu from './components/layout/MobileMenu';

//   import
import { pageMapping, getDefaultPage } from './utils/pageMapping';

function App() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [activeSection, setActiveSection] = useState('api-account');

  //    (?embed=true)
  const [isEmbed, setIsEmbed] = useState(() => {
    const urlParams = new URLSearchParams(window.location.search);
    return urlParams.get('embed') === 'true';
  });
  const [sidebarSections, setSidebarSections] = useState({
    'getting-started': false, //   
    'member-management': false,
    'messaging': false,
    'payment': false,
    'board': false,
    'push': false,
    'analytics': false,
    'settings': false,
    'api-docs': true, //   API  
    'developer': false,
    'help': false
  });

  //   
  const toggleSidebar = () => {
    setIsSidebarOpen(!isSidebarOpen);
  };

  //    
  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  //    
  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  //       ( )
  const toggleSidebarSection = (sectionKey) => {
    setSidebarSections(prev => {
      const newSections = {};
      
      //    
      Object.keys(prev).forEach(key => {
        newSections[key] = false;
      });
      
      //    (  )
      newSections[sectionKey] = !prev[sectionKey];
      
      return newSections;
    });
  };

  //       (  )
  const setActiveLink = (sectionId) => {
    setActiveSection(sectionId);
    
    //    
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  };

  //     -    
  const handleCategoryNavigation = (category) => {
    const defaultPage = getDefaultPage(category);
    setActiveLink(defaultPage);
    
    //     
    if (category === 'user-guide') {
      setSidebarSections(prev => ({
        ...Object.keys(prev).reduce((acc, key) => ({ ...acc, [key]: false }), {}),
        'getting-started': true
      }));
    } else if (category === 'developer') {
      setSidebarSections(prev => ({
        ...Object.keys(prev).reduce((acc, key) => ({ ...acc, [key]: false }), {}),
        'api-docs': true
      }));
    } else if (category === 'help') {
      setSidebarSections(prev => ({
        ...Object.keys(prev).reduce((acc, key) => ({ ...acc, [key]: false }), {}),
        'help': true
      }));
    }
  };

  //     
  const getCurrentPageCategory = () => {
    const currentPage = pageMapping[activeSection];
    return currentPage ? currentPage.category : 'user-guide';
  };

  //    /  
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 768) {
        setIsSidebarOpen(false);
        setIsMobileMenuOpen(false);
      }
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // body   (   )
  useEffect(() => {
    if (isMobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }

    //     
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isMobileMenuOpen]);

  //     
  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (window.innerWidth <= 768 && 
          isSidebarOpen && 
          !e.target.closest('.sidebar') && 
          !e.target.closest('.mobile-menu-btn')) {
        setIsSidebarOpen(false);
      }
    };

    document.addEventListener('click', handleOutsideClick);
    return () => document.removeEventListener('click', handleOutsideClick);
  }, [isSidebarOpen]);

  // activeSection     ( )
  useEffect(() => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth'
    });
  }, [activeSection]);

  return (
    <div className={`App ${isEmbed ? 'embed-mode' : ''}`}>
      {/*  -    */}
      {!isEmbed && (
        <Header
          onToggleSidebar={toggleSidebar}
          onToggleMobileMenu={toggleMobileMenu}
          activeSection={activeSection}
          onCategoryNavigation={handleCategoryNavigation}
          currentCategory={getCurrentPageCategory()}
          isMobileMenuOpen={isMobileMenuOpen}
        />
      )}

      {/*   */}
      <div className={`main-layout ${isEmbed ? 'embed-mode' : ''}`}>
        {/*  */}
        <Sidebar
          isOpen={isSidebarOpen}
          sections={sidebarSections}
          activeSection={activeSection}
          onToggleSection={toggleSidebarSection}
          onSetActive={setActiveLink}
          isEmbed={isEmbed}
        />
        
        {/*   */}
        <div className="content-area">
          {/*   */}
          <MainContent 
            activeSection={activeSection} 
            data-category={getCurrentPageCategory()}
          />
          
          {/*  (TOC) */}
          <TOC
            activeSection={activeSection}
            onSetActive={setActiveLink}
            pageType={getCurrentPageCategory()}
            isEmbed={isEmbed}
            key={activeSection} // activeSection   TOC 
          />
        </div>
      </div>

      {/*   */}
      <MobileMenu
        isOpen={isMobileMenuOpen}
        onClose={closeMobileMenu}
        onCategoryNavigation={handleCategoryNavigation}
        onSetActive={setActiveLink}
        currentCategory={getCurrentPageCategory()}
      />
    </div>
  );
}

export default App;